article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
video {
  display: inline-block;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden] {
  display: none;
}
html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}
a:focus {
  outline: thin dotted;
}
a:active,
a:hover {
  outline: 0;
}
h1 {
  font-size: 2em;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b,
strong {
  font-weight: 700;
}
dfn {
  font-style: italic;
}
mark {
  background: #ff0;
  color: #000;
}
code,
kbd,
pre,
samp {
  font-family: monospace, serif;
  font-size: 1em;
}
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}
q {
  quotes: \201c \201d \2018 \2019;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}
button,
input {
  line-height: normal;
}
button,html input[type=button],/* 1 */input[type=reset],input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
input[disabled] {
  cursor: default;
}
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
input[type="search"] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
textarea {
  overflow: auto;
  vertical-align: top;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
body,
figure {
  margin: 0;
}
legend,
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: "";
  clear: both;
  height: 0;
}

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.visuallyHidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(00 0 0);
  overflow: hidden;
}

/* REMOVE DEFAULTS & STANDARD STYLES =========== */
body,
h1,
h2,
h3,
p,
ul,
li {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  font-family: "El Messiri";
  line-height: 6rem;
  margin: 2rem 0;
  color: #232323;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 3.5rem;
}

p,
a {
  font-family: "Raleway";
  font-size: 1.8rem;
  line-height: 2.5rem;
  color: #232323;
}

a {
  text-decoration: none;
}

/* GLOBAL STYLES ==============================*/

html {
  font-size: 62.5%;
}

.wrapper {
  max-width: 1280px;
  width: 80%;
  margin: 0 auto;
}

body {
  font-family: "El Messiri", sans-serif;
  font-family: "Raleway", sans-serif;
}

img {
  width: 100%;
}

.color-1 {
  color: #eaf3fa;
}
.color-2 {
  color: #97dcd8;
}
.color-3 {
  color: #e8e8e8;
}
.color-4 {
  color: #ac345c;
}
.color-5 {
  color: #d1d3d5;
}
.color-6 {
  color: #232323;
}
.color-7 {
  color: #ffff;
}

.flex {
  display: flex;
}

.App {
  text-align: center;
  background-image: url("./assets/blue-snow.png");
  background-attachment: fixed;
}

/* SECTIONS -================================ */

.header,
.main {
  padding: 6rem 2rem;
}

/* HEADER ====================================*/

.header {
  height: 95vh;
  width: 30%;
}

.headerText {
  position: fixed;
  width: 25%;
}

h1 {
  position: relative;
}

h1::after {
  content: "";
  width: 30%;
  height: 0.5rem;
  background-color: #ac345c;
  position: absolute;
  bottom: -25px;
  left: 35%;
}

.intro {
  padding: 4rem 0;
  margin: 3rem 0;
}

/* MAIN ======================================= */

.main {
  flex-direction: column;
  width: 70%;
}

/* SEARCH SECTION ============================== */

.searchSection,
.gallerySection {
  padding-bottom: 8rem;
}

h2 {
  position: relative;
}

h2::after {
  content: "";
  width: 10%;
  height: 0.5rem;
  background-color: #ac345c;
  position: absolute;
  bottom: -25px;
  left: 45%;
}

.Search,
.Results {
  background-color: #ffff;
  font-family: "Raleway";
}

form {
  display: flex;
  align-items: center;
}

select {
  width: 60%;
  /* padding: 2rem; */
  height: 4rem;
  font-size: 1.6rem;
  text-indent: 5px;
  margin: 4rem 0;
  border: 1px solid #d1d3d5;
  box-shadow: 5px 5px 2px #e8e8e8;
}

.submitBtn {
  border: none;
  background-color: #97dcd8;
  padding: 1rem;
  margin: 2rem;
  border-radius: 5px;
  text-transform: uppercase;
  font-size: 1.6rem;
  font-weight: 600;
}

.submitBtn:hover {
  background-color: #ac345c;
  box-shadow: 5px 5px #e8e8e8;
  color: #eaf3fa;
}

/* RESULTS =================================== */

.galleryContainer {
  padding: 6rem 0;
}

.photo {
  padding: 2rem;
}

.credit {
  display: block;
  padding: 1rem;
}

.credit:hover {
  color: #ac345c;
  font-weight: 600;
}

.unsplash {
  display: block;
  padding: 1rem;
  font-style: italic;
  font-size: 1.8rem;
}

.unsplash:hover {
  color: #ac345c;
  font-weight: 600;
}

.download {
  /* font-size: 4rem; */
  padding: 1rem;
  color: #97dcd8;
}

.download:hover {
  color: #ac345c;
}

.downloadLarge {
  font-size: 5rem;
}

.downloadRegular {
  font-size: 4rem;
}

.downloadSmall {
  font-size: 3rem;
}

/* FOOTER =====================================*/

.footer {
  background-color: #d1d3d5;
  padding: 1rem 2rem;
  font-size: 1.2rem;
}

/* *************************************** */
/* MEDIA QUERIES 
********************************************/

@media (max-width: 1000px) {
  form {
    flex-direction: column;
  }
}

@media (max-width: 800px) {
  html {
    font-size: 55%;
  }

  .flex {
    flex-direction: column;
  }

  .header,
  .main,
  select {
    width: 100%;
  }

  .header,
  .main {
    padding: 3rem 2rem;
  }

  .header {
    height: auto;
  }

  .headerText {
    position: static;
    width: 100%;
  }

  .searchSection,
  .gallerySection {
    padding-bottom: 4rem;
  }
}
